import { createRouter, createWebHistory } from 'vue-router'
import HomeBe from '../views/HomePage_be.vue'
import HomeAf from '../views/HomePage_af.vue'
import Login from '../views/LoginPage.vue'
import dateA from '../views/230504Page.vue'
import dateB from '../views/230508Page.vue'
import dateC from '../views/230518Page.vue'
import dateD from '../views/230519Page.vue'
import dateE from '../views/230526Page.vue'
import dateF from '../views/230530Page.vue'
import dateG from '../views/230615Page.vue'
import dateH from '../views/230623Page.vue'
import dateI from '../views/230705Page.vue'
import dateJ from '../views/230713Page.vue'
import breakup from '../views/BreakupPage.vue'
import dateK from '../views/230727Page.vue'
import letter from '../views/LetterPage.vue'

const routes = [
  {
    path: '/',
    component: HomeBe
  },
  {
    path: '/home',
    component: HomeAf
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/dateA',
    component: dateA
  },
  {
    path: '/dateB',
    component: dateB
  },
  {
    path: '/dateC',
    component: dateC
  },
  {
    path: '/dateD',
    component: dateD
  },
  {
    path: '/dateE',
    component: dateE
  },
  {
    path: '/dateF',
    component: dateF
  },
  {
    path: '/dateG',
    component: dateG
  },
  {
    path: '/dateH',
    component: dateH
  },
  {
    path: '/dateI',
    component: dateI
  },
  {
    path: '/dateJ',
    component: dateJ
  },
  {
    path: '/breakup',
    component: breakup
  },
  {
    path: '/dateK',
    component: dateK
  },
  {
    path: '/letter',
    component: letter
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior () { return { top: 0 } }
})

export default router
