<template>
  <headerview/>
  <hr>
  <div class="login">
    <div class="login-container">
      <h2>Login</h2>
      <form @submit.prevent="loginCheck">
        <input type="text" placeholder="ID" v-model="user_id" /><br />
        <input type="password" placeholder="Password" v-model="user_password" /><br />
        <input type="submit" value="로그인" />
      </form>
    </div>
  </div>
</template>

<script>
import headerview from '../components/HeaderView_be.vue'

export default {
  data () {
    return {
      user_id: '',
      user_password: ''
    }
  },
  methods: {
    loginCheck () {
      if (this.user_id === '') {
        alert('아이디를 입력하세요.')
        return 0
      } else if (this.user_password === '') {
        alert('비밀번호를 입력하세요.')
        return 0
      }

      if (this.user_id === '0426' && this.user_password === '5034') {
        alert('로그인 성공!')
        this.$router.push('/home')
      } else {
        alert('아이디 또는 비밀번호가 틀렸습니다.')
        this.user_id = ''
        this.user_password = ''
      }
    }
  },
  components: {
    headerview
  }
}
</script>

  <style scoped>
  .login {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .login-container {
    text-align: center;
    width: 300px;
    padding: 40px 0;
  }

  h2 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #212121;
  }

  input {
    width: 100%;
    height: 48px;
    padding: 0 10px;
    box-sizing: border-box;
    margin-bottom: 16px;
    border-radius: 6px;
    background-color: #F8F8F8;
    border: none;
  }

  input::placeholder {
    color: #D2D2D2;
  }

  input[type="submit"] {
    color: #fff;
    font-size: 16px;
    background-color: #212121;
    margin-top: 20px;
  }
  input[type="submit"]:hover {
    cursor: pointer;
  }
  </style>
