      <h2>포항 여행</h2>
      <h3>날짜 [2023-06-15 ~ 2023-06-18]</h3>
      <p>거의 2주가 지나고 드디어 지현이를 만나러 포항으로 출발했다. 난 ktx를 타고 포항으로 갔다.</p>

      <p>오랜 시간동안 기차를 타고 포항역에 도착 했을 때 지현이가 서프라이즈로 날 기다리고 있었다. 딱 첨 보자마자 너무 귀엽고 예쁜 사람이다라고 생각했다.</p>

      <p>첫 날에는 시간도 늦었고 우리가 먹을 장을 보러 갔다. 나름 신혼 느낌으로 이것저것 사고 집으로 향했다.</p>

      <p>딱 집에 도착했을 때 강아지들이 엄청 짖었다. 난 어렸을 때 강아지가 무서웠어서 강아지만 보면 도망갔던 기억이 있다. 하지만 지금은 별로 무섭지 않았다. 머리가 커서 그런 것 같다.</p>

      <img src="../assets/230615/1.jpg">

      <p>강아지 종은 말티푸이다. 첫 느낌은 작은데 엄청 짖길래 무서움을 모르는 친구구나라고 생각했다.</p>

      <p>강아지가 2마리가 있는데, 한 마리는 팔팔한 청년이고 다른 한 마리는 노견이라서 사실 우리가 여기서 지내는 동안 무슨 일이 생기면 어쩌지?라는 걱정을 많이 했다.</p>

      <p>하지만 생각외로 매우 강인했다. 별 일도 없었고 밥도 잘 먹었다.</p>

      <p>그렇게 첫 날이 지나고 둘째 날에 우리는 미리 계획했던 경주에 가기로 했다. 경주에 가기 전, 지현이가 맛있는 샤브샤브 집으로 데려 갔다. 큰어머니께서 사주신 밥이다.</p>

      <img src="../assets/230615/2.jpg">

      <p>이제 막 집에서 나온 후에 찍은 사진이다. 후일담이지만 나중에는 꼴이 말이 아니라서 지금이 가장 뽀송한 상태이다.</p>

      <img src="../assets/230615/3.jpg">
      <br>
      <img src="../assets/230615/4.jpg">

      <p>월남쌈이라서 사실 큰 기대는 안 했다. 난 내가 월남쌈을 별로 안 좋아하는 줄 알았다.</p>

      <p>조금 먹다보니 너무 너무 너무 맛있었다. 그래서 월남쌈을 학살해버렸다.</p>

      <p>그렇게 죄없는 월남쌈들을 학살한 후 볼록 튀어나온 배를 통통 튀기며 우린 경주로 향했다.</p>

      <p>우린 맨 처음 대릉원에 가기 전에 황리단 길을 걸었다. 지현이가 준비한 양산을 쓰고 걸었다. 사실 난 양산을 써보기 전까지 그런게 왜 필요할까 싶어서 의아했는데, 그 당시 날씨를 보자마자 지현이의 선택이 탁월했음을 느꼈다.</p>

      <p>거기서 많은 것들을 먹었다. 탕후루, 쫀드기 등등 날이 더워서 카페에도 가고 했던 걸 생각하면 그냥 먹으러 간 것 같다.</p>

      <p>한참 길을 걷다가 기념품 가게? 같은 곳이 있었다.</p>

      <img src="../assets/230615/5.jpg">

      <p>지현이가 평소에 나를 위해 많은 걸 사주기도 했고, 내가 사준 게 별로 없는 것 같아서 반 강제적으로 강매했던 악세서리다.</p>

      <img src="../assets/230615/6.jpg">
      <br>
      <img src="../assets/230615/7.jpg">

      <p>우리의 소중한 디카에다가 보관하기로 했다.</p>

      <p>사실상 지현이가 고른거긴 한데 엄청 맘에 들고 고마워했다. 나중에 여유로워지면 산더미마냥 사주기로 다짐했다.</p>

      <p>황리단 길에서 놀다가 자연스레 대릉원으로 갔다. 그쪽에는 무덤이 되게 많았는데, 완전 핫플인 곳에 사람들이 사진 찍는다고 모여있었다. 우린 날도 덥고 지현이는 이미 찍었기 때문에 패스하기로 했다.</p>

      <img src="../assets/230615/8.jpg">

      <p>대릉원 안에 있는 공터같은 곳에서 날이 좀 풀리길 기다리며 찍은 사진이다.</p>

      <img src="../assets/230615/9.jpg">

      <p>대릉원에서 나와서 밥 먹으러 가면서 찍은 사진이다. 벌써부터 몰골이 나빠지고 있다.</p>

      <img src="../assets/230615/10.jpg">
      <br>
      <img src="../assets/230615/11.jpg">

      <p>밥은 지현이가 추천한 일본식 덮밥집으로 갔다.</p>

      <p>역시 지현이가 추천한 것은 최소 반 이상은 가는 듯 했다. 너무 맛있게 잘 먹었다. 네이버 리뷰로 음료수까지 받아서 알차게 먹었던 것 같다.</p>

      <p>다음으로 첨성대에 가기 전에 우리가 인생네컷도 찍었었다.</p>

      <img src="../assets/230615/12.jpg">

      <p>여행의 즐거움과 설렘이 나타난 듯 굉장히 자연스럽게 잘 찍혔다. 한동안 이걸 배사로 했었다.</p>

      <img src="../assets/230615/13.jpg">

      <p>밤이 되고 우린 첨성대에서 사진을 찍었다. 확실히 밤이라서 더 예쁜 탓인가, 사람들도 엄청 많았다.</p>

      <p>거기서 한 커플을 잡고 우리 사진도 찍어달라고 하면서 우리도 찍어주는 식으로 등가교환을 해보려고 했는데, 내가 너무 소심해서 그렇게 하진 못 했다.</p>

      <p>사실 문제는 지금부터였다. 내가 사람들이 너무 많은거에 점점 질려서 그만 여행 도중에 짜증을 낸 것이다.</p>

      <img src="../assets/230615/14.jpg">

      <p>문제 발생은 동궁과 월진에서 일어났다. 내가 기분이 안 좋아서 특유의 삐짐을 묵언으로 표출했고, 지현이는 영문도 잘 모른채 기분이 안 좋아보이는 나를 풀어주려고 계속 노력했다.</p>

      <p>사실 그 때의 행동이 아직까지 마음에 걸린다. 뭐가 어려운 거라고 사진도 안 찍고 괜히 고집을 부렸는지 모르겠다. 지현이한테 정말 미안했다.</p>

      <img src="../assets/230615/15.jpg">

      <p>표정에서 심술이 가득난게 보인다. 내 자신이 약간 미워진다.</p>

      <img src="../assets/230615/16.jpg">

      <p>그렇게 내 심술로 인해 서로 기분이 상해서 말 없이 걸어오는 길에 지현이가 먼저 문제를 말해줬고, 그제서야 나도 속마음을 전부 털어놨던 것 같다.</p>

      <p>이런 부분을 보면 참 지현이가 멋지고 성숙하다는 생각을 많이 한다. 내가 너무 어리광부리고 내 맘대로 안 된다고 심술을 냈던 것 같다.</p>

      <img src="../assets/230615/17.jpg">

      <p>그렇게 집에와서 서로 잘 화해하고 씻고 팩을 하는데 너무 웃겼다. 진짜 한동안 계속 웃었던 것 같다. 글을 작성하는 지금도 얕게 미소를 띄고 있다.</p>

      <img src="../assets/230615/18.jpg">
      <br>
      <img src="../assets/230615/19.jpg">

      <p>다음 날에는 포항에 왔으니 바다를 보러 갔다.</p>

      <p>지현이가 사진을 잘 찍어줬다. 이 날은 어제의 그러한 일들을 만회하고자 나도 열심히 사진을 담기 위해 노력했다. 지현이도 나를 많이 배려해줬다.</p>

      <img src="../assets/230615/20.jpg">

      <p>해수욕장 중간에 정자 같은 곳이 있어서 거기서 촬영을 했다.</p>

      <p>확실히 날이 더워서 그런가 사람들이 많이 몰려있었다.</p>

      <p>그리고 신기한 장면도 목격했다.</p>

      <img src="../assets/230615/21.jpg">

      <p>처음엔 버스킹하는 줄 알고 있었는데, 관객 호응 소리가 엄청 크길래 난 저게 뭘까 하면서 말을 좀 거칠게 했던 것 같다. 지현이가 듣고 왜 그러냐고 그랬는데, 자세히가서 보니까 신천지였다.</p>

      <p>지현이도 그걸 듣자마자 날 옹호했던 것 같다. 우리는 신천지에 좋지 않은 감정을 가지고 있기 때문이다.</p>

      <p>근데 진짜 웃기긴 했다. 자기들끼리 호응하고 노래부르고 난리였다. 그래서 내가 호응 1팀, 전도 2팀 이런식으로 전담반을 꾸려줬다.</p>

      <img src="../assets/230615/22.jpg">

      <p>날이 좀 풀리고 밤엔 바다를 들어가기로 했다. 발만 담궜는데 엄청 차가웠다.</p>

      <p>우리 말고도 몇몇 커플이 우리처럼 발을 담구기도 했는데, 지현이랑 이렇게 나란히 걷기만 해도 너무 행복했다.</p>

      <img src="../assets/230615/23.jpg">

      <p>집 가기전에 신천지가 버스킹 하던 곳 주변에 정원이 있어서 사진을 많이 찍었다.</p>

      <p>내가 가장 좋아하는 지현이 최애 짤이다. 한동안 플립 화면이 저거였다.</p>

      <img src="../assets/230615/24.jpg">

      <p>마지막으로 경주에서 찍었던 영문 모를 사람의 초상화이다.</p>

      <p>사실 훨씬 더 많은 일들이 있었는데, 최대한 짧게 쓰려고 주요 내용만 썼던 것 같다. 포항에서 있었던 모든 일들이 난 정말 행복했다.</p>

      <p>지현이랑 같이 잠들고 아침에 눈 뜨면 옆에서 일어난다는 것이 그렇게 행복한 줄은 몰랐다. 나도 너무 편하게 잤고, 지현이도 분명 그랬을 것이다.</p>

      <p>이 여행 이후로 우리가 같이 잔다는 것이 얼마나 행복한 일인지 알았다. 다음 여행이 계속 그리워졌다.</p>

      <p>포항에 남겨둔 강쥐들이 가끔 그립기도 하고, 지현이와 소소한 행복들이 그립기도 했다.</p>

      <p>첫 여행치고 만족스러운 여행이었고, 서로를 많이 배려하고 이해해준 여행이 된 것 같아서 매우 좋았다.</p>
